import React from 'react';

import {  Icon } from 'rsuite';

//Componentes de roteamento
import { Link } from "react-router-dom";

import logo from './../assets/logo.jpg';



export default class Infos extends React.Component {
    render() {
            return (
                <div className="dialogo col">
                    <div className="row center containerdialogoFixo">
                    <div className='dialogoFixo bkColor3 '>
                        <div className="logo border1">
                            <img src={logo} alt="Logo" />
                        </div>
                    </div>
                </div>
                    <div className='row center'>
                        <div className='dialogoContent bkColor2 Color2'>
                            <div className="dialogoMain col-md center fullWidth center">
                                <div className="col l6">
                                    {this.props.col1} 
                                </div>
                                <div className="col l6">
                                    {this.props.col2} 
                                </div>
                            </div>
                            <div className=" dialogoRodape row center fullWidth end">
                                {this.props.volta}
                            </div>
                        </div>
                        
                    </div>
                        
                </div>
            );
    }
}